.post-link{border:1px solid #444;border-radius:3px;margin:2rem;max-width:860px;padding:1rem}.post-link a{color:#000;text-decoration:none}.post-link .title{color:#555;display:inline-block;margin:0;padding-bottom:.5rem}.post-link .tags-list{display:flex;flex-direction:row;margin-bottom:.5rem;margin-left:1rem}.post-link .tags-list .tag{background-color:grey;border-radius:3px;color:#fff;margin-right:1rem;padding:3px 5px}.post-link .excerpt{color:#555;display:inline-block;padding-left:1rem;word-break:break-all}